.btns input {
  margin: 20px 0;
  color: #3c4245;
  padding: 5px 15px;
  border: 1px solid #dfcdc3;
  background-color: #dfcdc3;
  cursor: pointer;
  font-size: 16px;
}

.btns input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
</style>
