import { wait } from "./help.js";

/**
 * 
 * 
 */
export default class GameStateManager {
  constructor(options = {}) {
    // 
    this.hitflag = true; // 
    this.lockflag = true; // 
    this.winflag = false; // 
    this.loseflag = false; // 
    this.drawflag = false; // 

    // 
    this.history = [];

    // 
    this.isAutoRunning = false;
    this.autoInterval = null;
    this.autoStepDelay = options.autoStepDelay || 500; // 500ms

    // 
    this.listeners = {};
  }

  /**
   * 
   */
  init() {
    this.hitflag = true;
    this.lockflag = true;
    this.winflag = false;
    this.loseflag = false;
    this.drawflag = false;
    this.history = [];
    this.stopAuto();
    this.emit("init");
  }

  /**
   * 
   * @param {Object} operation - 
   */
  recordOperation(operation) {
    this.history.push(operation);
    this.emit("historyUpdate", this.history);
  }

  /**
   * 
   * @returns {Object|null} null
   */
  undo() {
    if (this.history.length === 0) {
      return null;
    }

    const operation = this.history.pop();
    this.emit("undo", operation);
    this.emit("historyUpdate", this.history);

    // 
    this.winflag = false;
    this.loseflag = false;
    this.drawflag = false;

    return operation;
  }

  /**
   * 
   * @returns {number} 
   */
  getStepCount() {
    return this.history.length;
  }

  /**
   * 
   * @returns {boolean} 
   */
  canUndo() {
    return this.history.length > 0 && this.hitflag && this.lockflag;
  }

  /**
   * 
   * @param {Function} stepCallback - 
   */
  async startAuto(stepCallback) {
    if (this.isAutoRunning || this.winflag || this.loseflag || this.drawflag) {
      return;
    }

    this.isAutoRunning = true;
    this.lockflag = false;
    this.emit("autoStart");

    try {
      while (
        this.isAutoRunning &&
        !this.winflag &&
        !this.loseflag &&
        !this.drawflag
      ) {
        await stepCallback();
        await wait(this.autoStepDelay);
      }
    } catch (error) {
      console.error("Auto mode error:", error);
    } finally {
      this.stopAuto();
    }
  }

  /**
   * 
   */
  stopAuto() {
    if (!this.isAutoRunning) {
      return;
    }

    this.isAutoRunning = false;
    if (this.autoInterval) {
      clearInterval(this.autoInterval);
      this.autoInterval = null;
    }

    this.emit("autoStop");
  }

  /**
   * 
   * @param {Function} stepCallback - 
   */
  async step(stepCallback) {
    if (
      !this.hitflag ||
      this.winflag ||
      this.loseflag ||
      this.drawflag
    ) {
      return;
    }

    this.hitflag = false;
    try {
      await stepCallback();
    } catch (error) {
      console.error("Step error:", error);
    } finally {
      this.hitflag = true;
    }
  }

  /**
   * 
   */
  setWin() {
    this.winflag = true;
    this.hitflag = false;
    this.stopAuto();
    this.emit("win");
  }

  /**
   * 
   */
  setLose() {
    this.loseflag = true;
    this.hitflag = false;
    this.stopAuto();
    this.emit("lose");
  }

  /**
   * 
   */
  setDraw() {
    this.drawflag = true;
    this.hitflag = false;
    this.stopAuto();
    this.emit("draw");
  }

  /**
   * 
   * @param {Function} resetCallback - 
   */
  reset(resetCallback) {
    this.stopAuto();
    this.init();
    if (resetCallback) {
      resetCallback();
    }
    this.emit("reset");
  }

  /**
   * 
   * @param {string} event - 
   * @param {Function} listener - 
   */
  on(event, listener) {
    if (!this.listeners[event]) {
      this.listeners[event] = [];
    }
    this.listeners[event].push(listener);
  }

  /**
   * 
   * @param {string} event - 
   * @param {Function} listener - 
   */
  off(event, listener) {
    if (!this.listeners[event]) {
      return;
    }
    const index = this.listeners[event].indexOf(listener);
    if (index > -1) {
      this.listeners[event].splice(index, 1);
    }
  }

  /**
   * 
   * @param {string} event - 
   * @param {*} data - 
   */
  emit(event, data) {
    if (!this.listeners[event]) {
      return;
    }
    this.listeners[event].forEach(listener => {
      try {
        listener(data);
      } catch (error) {
        console.error(`Error in event listener for ${event}:`, error);
      }
    });
  }

  /**
   * 
   * @returns {Object} 
   */
  getState() {
    return {
      hitflag: this.hitflag,
      lockflag: this.lockflag,
      winflag: this.winflag,
      loseflag: this.loseflag,
      drawflag: this.drawflag,
      step: this.getStepCount(),
      isAutoRunning: this.isAutoRunning
    };
  }

  /**
   * 
   * @param {Object} state - 
   */
  setState(state) {
    Object.assign(this, state);
    this.emit("stateChange", this.getState());
  }
}
